.hero {
  padding: 0;

  @media screen and (min-width: $tablet-min) {
    padding: 35px 0;
  }
}

.hero__overlay {
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: $mobile-medium) {
    // padding-left: 40px;
    // padding-right: 40px;
    background-image: url('../images/bouquet/bouquet1-1x-mob.png'),
      url('../images/bouquet/Ellipse2-1x-mob.png'),
      url('../images/bouquet/Ellipse3-1x.png'),
      url('../images/bouquet/Ellipse3-1x.png'),
      url('../images/bouquet/Ellipse1-1x-mob.png'),
      url('../images/bouquet/bouquet2-1x-mob.png'),
      url('../images/bouquet/bouquet3-1x.png'),
      url('../images/bouquet/bouquet4-1x.png');
    background-repeat: no-repeat;
    background-position: top right, top 182px right 188px, top 84px right 356px,
      top 84px right 35px, top right 20px, top 211px right 160px,
      top 145px right 20px, top 145px right 320px;
  }

  @media screen and (min-width: $tablet-min) {
    background-image: url('../images/bouquet/bouquet1-1x-desk.png'),
      url('../images/bouquet/Ellipse2-1x-desk.png'),
      url('../images/bouquet/Ellipse3-1x.png'),
      url('../images/bouquet/Ellipse3-1x.png'),
      url('../images/bouquet/Ellipse1-1x-desk.png'),
      url('../images/bouquet/bouquet2-1x-desk.png'),
      url('../images/bouquet/bouquet3-1x.png'),
      url('../images/bouquet/bouquet4-1x.png');
    background-repeat: no-repeat no-repeat;
    background-position: right center, right 298px center, top 60px right 268px,
      bottom 60px right 268px, right center, right 326px center,
      right 290px bottom 30px, right 290px top 30px;
  }
}

.hero__content {
  @media screen and (min-width: $mobile-medium) {
    margin-left: auto;
    margin-right: auto;
    padding-top: 313px;
    padding-bottom: 40px;
    width: 325px;
    text-align: center;
  }

  @media screen and (min-width: $tablet-min) {
    margin-left: 0;
    margin-right: 0;
    padding-top: 128px;
    padding-bottom: 70px;
    width: 303px;
    text-align: left;
    // font-weight: 400;
  }
}

.hero__title {
  @extend %title-section;

  margin-bottom: 10px;

  @media screen and (min-width: $tablet-min) {
    margin-bottom: 13px;
  }
}

.hero__title-description {
  display: none;

  @media screen and (min-width: $tablet-min) {
    display: block;
    margin-bottom: 17px;
  }
}

.hero__title-description-mob {
  margin-bottom: 20px;

  @media screen and (min-width: $tablet-min) {
    display: none;
  }
}

.hero__buttons .button-primary:not(:first-child) {
  margin-left: 22px;
}
